<template>
  <view class="pageStyle">
    <web-view :src="urls"></web-view>
  </view>
</template>
<script>
import { Api } from "@/utils/Api.js";
import { mapState } from "vuex";

export default {
  data() {
    return {
      describe: "通用编辑",
      url: {
        list: "/weixin/visual/module_add_edit.jsp",
      },
      urls: "",
    };
  },
  onLoad(options) {
    let records = JSON.parse(options.record);
    //动态获取webview链接
    if (records.id) {
      //编辑
      this.urls =
        Api.baseUrl +
        this.url.list +
        "?skey=" +
        this.loginInfo.skey +
        "&moduleCode=" +
        records.code +
        "&id=" +
        records.id +
        "&isUniWebview=true";
    } else {
      this.urls =
        Api.baseUrl +
        this.url.list +
        "?skey=" +
        this.loginInfo.skey +
        "&moduleCode=" +
        records.code +
        "&isUniWebview=true";
    }
  },
  computed: {
    ...mapState(["loginInfo"]),

    // urls() {
    //   let urls = Api.baseUrl + this.url.list + this.loginInfo.skey;
    //   return urls;
    // },
  },
};
</script>

<style lang="scss" scoped>
